<template>
  <div class="uploadFiles">
    <!-- <el-dialog :visible.sync="dialogVisible">
      <img :key="dialogImageUrl" width="100%" :src="dialogImageUrl" alt />
    </el-dialog>-->
    <!--  -->
    <!-- <el-dialog title="" :visible.sync="chaochudialogVisible" width="30%">
      <span>{{tishi}}</span>
      <span slot="footer" class="dialog-footer">
        <el-button @click="chaochudialogVisible = false"> </el-button>
        <el-button type="primary" @click="chaochudialogVisible = false"> </el-button>
      </span>
    </el-dialog>-->
    <el-upload
      class="upload-demo"
      action="/"
      :limit="limit"
      :file-list="fileListSVG"
      :http-request="uploadSVG"
      :before-upload="beforeAvatarUpload"
      :on-error="failureMethods"
      :on-change="handleChange"
    >
      <el-button size="small" type="primary"></el-button>
      <div slot="tip" class="el-upload__tip">20MSVG</div>
    </el-upload>
  </div>
</template>

<script>
import { getToken } from '@/utils/auth'
export default {
  name: "UploadSvg",
  props: {
    //
    currenItem: {},
  },
  data() {
    return {
      cheSVG: "",
      limit: 1,
      //
      header: {
        headers: {
          "Content-Type":
            "multipart/form-data; boundary=--------------------------AAAAA",
            "AccessToken": getToken()
        },
      },
      clentSts: {},
      fileListSVG: [],
      dialogImageUrl: "",
      dialogVisible: false,
      chaochudialogVisible: false,
      tishi: "",
      isAdd: true,
      //
      is_confirmed: false,
    };
  },
  methods: {
    //
    beforeAvatarUpload(file) {
      const isLt20M = file.size / 1024 / 1024 < 20;
      if (!isLt20M) {
        this.$message({
          message: "SVG 20MB!",
          type: "warning",
        });
      }
      return isLt20M;
    },
    //  
    handleChange(file, fileList) {
      this.fileListSVG.push(file.raw);
    },
    /*  - SVG */
    uploadSVG(option) {
      var _this = this;
      var upData = new FormData();
      upData.append("upload", this.fileListSVG[0]);
      upData.append("merchant_id", _this.currenItem.merchant_id);
      upData.append("shop_id", _this.currenItem.shop_id);
      upData.append("is_confirmed", _this.is_confirmed);
      this.cheSVG = this.fileListSVG;
      this.$ajax
        .post("/shopset/shop/uploadSvg", upData, this.header)
        .then((res) => {
          if (res.data.code != 0) {
            //
            this.$confirm(res.data.msg, "", {
              confirmButtonText: "",
              cancelButtonText: "",
              type: "warning",
            })
              .then(() => {
                _this.is_confirmed = true;
                _this.uploadSVG();
              })
              .catch(() => {
                _this.fileListSVG = [];
                _this.is_confirmed = false;
              });
          } else {
            this.fileListSVG = [];
            this.is_confirmed = false;
            this.$emit("theSVG", res.data.data.url);
          }
        });
    },
    //
    failureMethods() {
      this.$message({
        message: "SVG",
        type: "warning",
      });
    },
  },
};
</script>

<style lang="scss" scoped>
.uploadFiles {
  position: relative;
  .upload {
    width: 300px;
    height: 200px;
    border: 1px solid #eee;
    text-align: center;
    cursor: pointer;
    transition: all 0.2 linear;
    &:hover {
      border: 1px solid #ccc;
      i {
        margin-top: 18px;
      }
    }
    i {
      margin-top: 20px;
      font-size: 100px;
    }
  }
  .avatar {
    width: 100%;
  }
  .el-upload-dragger {
    height: auto !important;
  }
}
</style>
